import React, { createContext, useContext, useState, useEffect } from 'react';
import { SUPPORTED_LOCALES, getBrowserLocale, getLocaleMessages, translate } from '../locales';

interface LanguageContextType {
  language: string;
  setLanguage: (lang: string) => void;
  t: (key: string, fallback?: string) => string;
  locales: typeof SUPPORTED_LOCALES;
}

const LanguageContext = createContext<LanguageContextType | undefined>(undefined);

export function LanguageProvider({ children }: { children: React.ReactNode }) {
  // ä»localStorageæˆ–æµè§ˆå™¨è¯­è¨€ä¸­è·å–åˆå§‹è¯­è¨€è®¾ç½®
  const [language, setLanguage] = useState(() => {
    // æ£€æŸ¥æ˜¯å¦é¦–æ¬¡è®¿é—®
    const hasVisited = localStorage.getItem('gameverse-has-visited');
    const savedLanguage = localStorage.getItem('gameverse-language');
    
    // å¦‚æœæ˜¯é¦–æ¬¡è®¿é—®ï¼Œæ ¹æ®æµè§ˆå™¨è¯­è¨€è‡ªåŠ¨è®¾ç½®ï¼Œå¹¶è®°å½•è®¿é—®æ ‡è®°
    if (!hasVisited) {
      const detectedLanguage = getBrowserLocale(true); // ä¼ å…¥trueè¡¨ç¤ºå¿½ç•¥localStorageä¸­çš„å€¼
      localStorage.setItem('gameverse-has-visited', 'true');
      localStorage.setItem('gameverse-language', detectedLanguage);
      return detectedLanguage;
    }
    
    // éé¦–æ¬¡è®¿é—®ï¼Œä¼˜å…ˆä½¿ç”¨ç”¨æˆ·å·²ä¿å­˜çš„è¯­è¨€è®¾ç½®
    return savedLanguage || 'en';
  });

  // å½“è¯­è¨€å˜åŒ–æ—¶æ›´æ–°æ–‡æ¡£è¯­è¨€å’Œæ–¹å‘
  useEffect(() => {
    document.documentElement.lang = language;
    
    // å¦‚æœæ˜¯RTLè¯­è¨€ï¼ˆå¦‚é˜¿æ‹‰ä¼¯è¯­ï¼‰ï¼Œè®¾ç½®æ–‡æ¡£æ–¹å‘
    if (language === 'ar') {
      document.documentElement.dir = 'rtl';
    } else {
      document.documentElement.dir = 'ltr';
    }
  }, [language]); // æ·»åŠ languageä½œä¸ºä¾èµ–é¡¹

  // æ›´æ–°è¯­è¨€
  const handleLanguageChange = (lang: string) => {
    if (lang === language) return; // å¦‚æœè¯­è¨€ç›¸åŒï¼Œä¸åšä»»ä½•æ“ä½œ
    
    setLanguage(lang);
    localStorage.setItem('gameverse-language', lang);
  };

  // ç¿»è¯‘å‡½æ•°
  const t = (key: string, fallback?: string) => {
    return translate(language, key, fallback);
  };

  return (
    <LanguageContext.Provider 
      value={{ 
        language, 
        setLanguage: handleLanguageChange, 
        t,
        locales: SUPPORTED_LOCALES
      }}
    >
      {children}
    </LanguageContext.Provider>
  );
}

export function useLanguage() {
  const context = useContext(LanguageContext);
  if (context === undefined) {
    throw new Error('useLanguage must be used within a LanguageProvider');
  }
  return context;
}